/* Reset some default styling */
body, ul {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-image: url('/src/background.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center center;
    font-family: Archivo, sans-serif;
    overflow-x: hidden;
}

/* Style the header */
header {
    position: relative; /* Add relative positioning */
    z-index: 10; /* Set a higher z-index value for the header */
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    font-family: 'Archivo', serif;
    color: #598392;
}

.logo {
    max-width: 300px;
}

.header-text h1, .header-text p {
    margin: 0;
}

.header-text h1 {
    font-size: 40px;
}

.header-text p {
    font-size: 20px;
}


.core-service {
    /* Set each service to occupy 50% of the available width (with spacing) */
    background-color: #598392; /* Choose your desired background color */
    color: #000000; /* Set the text color for better visibility */
    padding: 20px; /* Add padding for spacing */
    font-family: 'Archivo', sans-serif;
    font-size: 24px; /* Adjust font size as needed */
    margin-left: auto; /* Add margin to move it towards the right */
}

/* Style for the services section */
#services {
    display: grid;
    grid-template-columns: repeat(3, 3fr); /* Create three equal columns */
    gap: 20px; /* Add some gap between services */
    padding: 20px;
    font-family: 'Archivo', sans-serif;
    font-size: 16px;
    background-color: #598392; /* Set the line color */
}

/* Style for individual service items */
.service {
    background-color: #598392; /* Make the service background transparent */
    color: #000000;
    padding: 20px;
    font-family: 'Archivo', sans-serif;
    font-size: 16px;
    text-align: -moz-left; /* Center align service text */
}

/* Style for the h3 headings inside the .service class */
.service h3 {
    color: #000000; /* Set a different color for h3 headings */
    font-size: 24px;
}

/* Style for the p paragraphs inside the .service class */
.service p {
    color: #FFFFFF; /* Set a different color for p paragraphs */
}
/* Style for the contact form section */
#contact {
    background-color: #598392; /* Make the service background transparent */
    padding: 20px;
    font-family: 'Archivo', sans-serif;
    font-size: 16px;
    text-align: center;
}

#contact h2 {
    font-size: 36px;
    color: #000000; /* Set a different color for h3 headings */
}

#contact p {
    color: #FFFFFF; /* Set a different color for h3 headings */
}

#contact form input,

#contact form textarea {
    width: calc(100% - 20px);
    padding: 10px;
    margin: 5px 0 20px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-family: 'Archivo', sans-serif;
    font-size: 16px;
}

#contact form input[type="submit"] {
    cursor: pointer;
}

/* Style for the footer */
footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    font-family: 'Archivo', sans-serif;
    font-size: 16px;
}

.social-icons img {
    width: 24px;
    height: auto;
    margin: 0 5px;
}

/* Add this rule to create a horizontal line */
.horizontal-line {
    border: none; /* Remove default border */
    height: 2px; /* Set the desired height for the line */
    background-color: #598392; /* Set the line color */
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 50px; /* Adjust the margin as needed to create space */
    width: 85%;
}

.image-collage {
    width: 85%;
    height: 50%;
    border-radius: 50px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

/* Media query for responsiveness */
@media screen and (max-width: 600px) {
    header {
        flex-direction: column;
        align-items: flex-start;
    }

    .header-text h1 {
        font-size: 24px;
    }

    .header-text p {
        font-size: 14px;
    }

    #services {
        grid-template-columns: 1fr; /* Change to a single column layout for small screens */
    }
}